html body{
    &.woocommerce-shop, &.woocommerce, .catalog-page, .related-products-slider, .product-page{
        .products {
            width: 100%;
            @extend .items-general-wrapper;
            margin: 0;
            grid-template-columns: 1fr 1fr 1fr;
            align-items: center;
            @include breakpoint(xxll) {
                grid-template-columns: 1fr 1fr;
            }
            @include breakpoint(xl) {
                grid-template-columns: 1fr 1fr 1fr;
            }
            @include breakpoint(lg) {
                grid-template-columns: 1fr 1fr;
            }
            @include breakpoint(md) {
                gap: var(--spacing-2);
            }
            &::before {
                display: none;
            }
        }
        .product {
            @extend .item-general;
            width: 100% !important;
            float: none;
            display: block;
            position: relative;
            border-radius: 20px;
            border-radius: var(--radius_2) !important;
            @include breakpoint(xxl) {
                padding: 20px !important;
            }
            @include breakpoint(sm) {
                padding: 10px !important;
            }
            a img {
                max-width: 250px;
                margin: 10px auto 30px;
                z-index: -1;
                position: relative;
                display: block;
                @include breakpoint(md) {
                    max-width: 130px;
                }
            }
            >a{
                opacity: 1 !important;
            }
            .woocommerce-loop-product__title{
                font-family: Roboto;
                font-size: 26px;
                font-weight: 500;
                line-height: 34px;
                letter-spacing: 0.035em; 
                @include breakpoint(xxl-extra) {
                    font-size: 20px;
                    line-height: 1.6;
                }
                @include breakpoint(md) {
                    font-size: 14px;
                }
            }
            .price{
                font-family: Rubik;
                font-size: 32px;
                font-weight: 400;
                line-height: 29px;
                letter-spacing: 0.142547145485878px;
                text-align: left;
                @include breakpoint(xxl-extra) {
                    font-size: 24px;
                }
                @include breakpoint(md) {
                    font-size: 16px;
                    flex-direction: column;
                }
                del {
                    text-decoration: line-through rgba(51, 51, 51, 0.2);
                    font-size: 24px;
                    @include breakpoint(xxl-extra) {
                        font-size: 20px;
                    }
                    @include breakpoint(md) {
                        font-size:14px;
                        margin-right: 3px;
                    }
                    *{
                        color: rgba(51, 51, 51, 0.2);
                    }
                }
                ins {
                    text-decoration: none;
                    *{
                        color: rgba(51, 51, 51, 1);
                        margin-left: 5px;
                        @include breakpoint(md) {
                            margin-left: 0;
                            font-weight: 600;
                        }
                    }
                }
                
            }
            .button {
                @extend .lg;
                @extend .btn-primary;
                @extend .bright;
                width: 100%;
                &.desktop{
                    padding: 10px;
                    @include breakpoint(lg) {
                        display: none;
                    }
                }
                &.mobile{
                    display: none;
                    padding: 8px;
                    width: fit-content;
                    border-radius: 6px;
                    margin: 0;
                    @include breakpoint(lg) {
                        display: flex;
                    }
                }

            }
            .quantity {
                display: flex;
                justify-content: flex-start;
                align-items: center;
                width: fit-content;
                input{
                    text-align: center;
                    &.quantity-toggle-button{
                        font-size: 24px;
                        background: rgba(217, 217, 217, 0.58);
                        border-radius: 50%;
                        width: 40px;
                        height: 40px;
                        outline: none;
                        border: none;
                        @include breakpoint(xxl) {
                            width: 30px;
                            height: 30px;
                        }
                        &:hover{
                            background: rgba(0, 224, 79, 0.25);
                        }
                    }
                    &.input-text{
                       margin: 0;
                       width: 45px;
                       border: none;
                    }
                }
                input[data-action="minus"]{
                    font-size: 40px;
                }
                input[type="number"] {
                    -webkit-appearance: none;
                    -moz-appearance: textfield;
                    appearance: textfield;
                }
            }
        }
        .go-to-builder{
            width: fit-content;
            margin: 20px auto;
        }
    }
}

.catalog-page{
    .d-flex{
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
    }
    .filter-wrapper{
        @include transition();
        max-width: 350px;
        width: 100%;
        @include breakpoint(xl) {
            position: fixed;
            top: 0;
            z-index: 5;
            left: -100%;
        }
        &.opened{
            left: 0;
        }
        .filter{
            background: rgb(221, 221, 221);
            height: 100vh;
            overflow-x: hidden;
            overflow-y: auto;
            padding-top: calc(var(--header-height) + 100px);
            .close{
                position: absolute;
                right: 20px;
                display:none;
                @include breakpoint(xl) {
                    display: block;
                }
            }
        }
    }
    .product-list{
        width: 100%;
        padding: 0 40px;
        @include breakpoint(xl) {
            padding: 0;
        }
        .pagination{
            margin-top: 50px;
            @include breakpoint(md) {
                margin-top: 20px;
                min-height: 80px;
            }
        }
    }
}

.filter-btn{
    align-items: center;
    margin: 20px 0;
    display: none;
    @include breakpoint(xl) {
        display: flex;
    }
    svg{
        margin-left: 10px;
    }
}

.product-item{
    transition: all 0.7s ease-in-out;
    transition-delay: 0.5s;
    position: relative;
    height: 540px;
    max-height: 100% !important;
    @include breakpoint(xxl) {
        height: 490px;
    }
    @include breakpoint(lg) {
        height: fit-content;
    }
    &:hover{
        height: 645px;
        @include breakpoint(xxl) {
            height: 600px;
        }
        @include breakpoint(lg) {
            height: fit-content;
        }
        .quantity{
            opacity: 1;
        }
        .wish{
            transform: scale(1);
        }
    }
    .quantity{
        @include transition;
        opacity: 0;
        @include breakpoint(lg) {
            display: none !important;
        }
    }
    .wish{
        @include transition;
        margin-left: auto;
        position: relative;
        display: block;
        z-index: 2;
        opacity: 0.5;
        transform: scale(0);
        @include breakpoint(lg) {
            transform: scale(1);
        }
        &:hover{
            opacity: 0.3;
        }
        &.filled{
            opacity: 1;
            path{
                fill: #db0303;
                fill-opacity: 1;
            }
        }
    }
    .onsale{
        @include font-style('Poppins', 600, 16px, 24px);
        position: absolute;
        right: 0;
        background: #db0303;
        padding: 3px 10px;
        top: 60px;
        border-bottom-left-radius: 10px;
        border-top-left-radius: 10px;
        color: #fff;
        @include breakpoint(md) {
            padding: 3px 5px;
            border-bottom-left-radius: 6px;
            border-top-left-radius: 6px;
            font-size: 14px;
            top: 45px;
        }
    }
    .wrapper-line{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 20px 0;
        @include breakpoint(xxl) {
            margin: 10px 0;
        }
        @include breakpoint(md) {
            margin: 0;
            align-items: flex-end;
        }
        .price{
            display: flex;
            margin-left: 10px;
            @include breakpoint(md) {
                margin-left: 0;
            }
        }
    }
    .product-series{
        margin: 20px 0;
        @include font-style('Roboto', 300, 24px, 28px);
        @include breakpoint(xxl-extra) {
           font-size: 20px;
        }
        @include breakpoint(xxl) {
            font-size: 16px;
            margin: 10px 0;
        }
        @include breakpoint(md) {
            font-size: 14px;
            margin: 0;
        }
        @include breakpoint(sm) {
            font-size: 12px;
        }
    }
}

.swiper-button-next:after, .swiper-rtl .swiper-button-prev:after,
.swiper-button-prev:after, .swiper-rtl .swiper-button-next:after{
    color: #767676;
    font-size: 35px;
    @include breakpoint(sm) {
        font-size: 25px;
    }
}