.single-product, .product-page{
    .prod_wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}
.related-products-slider{
    position: relative;
    .products-slider{
        width: 90%;
        margin: 50px auto;
    }

    .product-item{
        border: 1px solid var(--color_transition);
        height: 450px;
        overflow: hidden;
        padding: 20px;
        @include breakpoint(xxl) {
            height: 410px;
        }
        @include breakpoint(lg) {
            height: fit-content;
        }
        &:hover{
            height: 530px; 
            @include breakpoint(xxl) {
                height: 500px;
            }
            @include breakpoint(lg) {
                height: fit-content;
            }
        }
    }

    .swiper-wrapper{
        align-items: center;
        padding-bottom: var(--spacing-12);
    }

    .swiper-button-prev{
        @include breakpoint(md) {
            top:12px;
            left: unset;
            right: 90px;
        }
    }
    .swiper-button-next{
        @include breakpoint(md) {
            top:12px;
            right: 25px;
        }
    }
}

.seo-text{
    padding-bottom: 20px;
    .title{
        margin-bottom: 20px;
    }
    .content-wrapper{
        @include transition;
        height: 150px;
        overflow: hidden;
        position: relative;
        &:after{
            content: '';
            width: 100%;
            height: 100%;
            bottom: 0;
            left: 0;
            position: absolute;
            background: rgb(255,255,255);
            background: linear-gradient(0deg,#fff 0%,rgba(255,255,255,.05) 100%);
        }
        &.opened{
            height: var(--el-height);
            &:after{
                opacity: 0;
                pointer-events: none;
            }
        }
    }
    .content {
        position: relative;
        max-height: fit-content;
        overflow: hidden;
    }
    .btn-primary{
        margin: 30px auto;
    }
}
.product-top{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-top: 20px;
    gap: 20px;
    > .column{
        width: 50%;
    }
    .label-heading{
        display: flex;
        justify-content: space-between;
    }
    .product-item{
        padding: 0 !important;
    }
}

.main-product-slider{
    .swiper {
        width: 100%;
        height: 100%;
      }
  
      .swiper-slide {
        text-align: center;
        font-size: 18px;
        background: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
      }
  
      .swiper-slide img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
  
      body {
        background: #000;
        color: #000;
      }
  
      .swiper {
        width: 100%;
        height: 300px;
        margin-left: auto;
        margin-right: auto;
      }
  
      .swiper-slide {
        background-size: cover;
        background-position: center;
      }
  
      .product-slider-mini {
        height: 80%;
        width: 100%;
      }
  
      .product-slider-preview {
        height: 20%;
        box-sizing: border-box;
        padding: 10px 0;
      }
  
      .product-slider-preview .swiper-slide {
        width: 25%;
        height: 100%;
        opacity: 0.4;
      }
  
      .product-slider-preview .swiper-slide-thumb-active {
        opacity: 1;
      }
  
      .swiper-slide img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
}