@import '../helpers/helpers';

.login{
    display: none;
    justify-content: center;
    &-popup{
        position: relative;
        width: 100%;
        max-width: 670px;
        border-radius: var(--radius_3);
        background: var(--color_secondary);
        padding: 40px 20px;
        box-shadow: 0px 8px 21px 0px rgba(51, 51, 51, 0.5);
        height: fit-content;
        @include breakpoint(md) {
            padding: 40px 10px;
        }
    }
    &-heading{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        margin: 20px 0 50px;
        @include breakpoint(md) {
            margin: 20px 0;
        }
        li{
            @include transition();
            will-change: font-size, border;
            @include font-style('Poppins', 600, 18px, 27px);
            border: 1px solid rgba(51, 51, 51, 0.6);
            border-bottom: 0;
            color: rgba(51, 51, 51, 1);
            min-height: 50px;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            text-transform: uppercase;
            @include breakpoint(md) {
                @include font-style('Poppins', 600, 12px, 18px);
            }
            &:not(.active){
                font-size: 14px;
                line-height: 21px;   
                color: rgba(51, 51, 51, 0.6);
                border-bottom: 1px solid rgba(51, 51, 51, 0.6);
                border-top: 0;
                @include breakpoint(md) {
                    font-size: 10px;
                    line-height: 18px;   
                }
            }
            &:first-child{
                &:not(.active){
                    border-right: 0;
                    border-left: 0;
                }
            }
            &:last-child{
                &:not(.active){
                    border-right: 0;
                    border-left: 0;
                }
            }
        }
        
    }
    &-title{
        @include font-style('Poppins', 600, 18px, 27px);
        text-align: center;
        text-transform: uppercase;
        margin: 10px auto;
    }
    .content-block{
        position: relative;
        &:not(.opened){
           display: none;
        }
    }
    .close{
        position: absolute;
        right: 20px;
        top: 20px;
        &:hover{
            opacity: 0.5;
        }
    }
    .forgot-pass{
        @include font-style('Roboto', 300, 14px, 18px);
        text-align: center;
        color: rgba(51, 51, 51, 1);
        display: block;
        margin: 40px auto 20px;
        @include breakpoint(md) {
            margin: 20px 0;
        }
    }
    .alternative{
        @include font-style('Poppins', 600, 20px, 30px);
        display: flex;
        align-items: center;
        justify-content:center;
        position: relative;
        text-transform: uppercase;
        @include breakpoint(md) {
            font-size: 11px;
        }
        &:after, &:before{
            content: '';
            position: absolute;
            left: 0;
            width: 40%;
            height:1px;
            background: rgba(51, 51, 51, 0.3);
        }
        &:before{
            left: unset;
            right: 0;
        }
    }
    .login-form{
        width: 80%;
        margin: 20px auto 0;
        display: none;
        @include breakpoint(md) {
            width: 100%;
        }
        .btn-primary{
            margin: 0 auto;
            width: 100%;
        }
    }
    .open-login-form{
        margin: 20px auto;
        &.hidden{
            display: none;
            + *{
                display: block;
            }
        }
    }
    .desc{
        @include font-style('Roboto', 300, 14px, 18px);
        text-align: center;
        padding: 20px;
    }
    .signUp-block{
        form{
            width: 80%;
            margin: 50px auto 20px;
            @include breakpoint(md) {
                width: 100%;
            }
            .btn-primary{
                width: 100%;
                margin: 0 auto;
            }
        }
        .alternative{
            margin-bottom: 20px;
           &:after, &:before{
            width: 30%;
           }
        }
    }
    .social{
        display: flex;
        align-items: center;
        justify-content: space-between;
        li{
            @include transition();
            @include font-style('Poppins', 600, 16px, 21px);
            color: rgba(51, 51, 51, 1);
            margin: 10px;
            padding: 10px 15px;
            width: 100%;
            border: 1px solid rgba(51, 51, 51, 1);
            display: flex;
            justify-content: center;
            align-items: center;
            @include breakpoint(md) {
                font-size: 10px;
                margin: 5px;
                padding: 5px 10px;
            }
            &:hover{
                opacity: 0.6;
            }
        }
    }
}

.login-to-account{
    background: rgba(159, 159, 159, 0.3);
    padding: 20px;
    display: flex;
    align-items: flex-start;
    margin: 20px 0;
    @include breakpoint(md) {
        padding: 20px 10px;
        padding-left: 0;
    }
    svg{
        margin-right: 10px;
        flex-shrink: 0;
        width: 50px;
        top: -7px;
        position: relative;
        @include breakpoint(md) {
            margin-right: 5px;
        }
    }
    .wrapper{
        p{
            @include font-style('Roboto', 600, 18px, 21px);
            @include breakpoint(md) {
                font-size: 16px;
            }
        }
        a{
            text-decoration: underline;
            font-weight: 400;
            margin: 0 10px
        }
        span{
            @include font-style('Roboto', 400, 16px, 18px);
            margin-top: 10px;
            display: block;
        }
    }
}

.products-mini{
    max-width: 580px;
    width: 100%;
    ul{
        li{
            display: flex;
            align-items: center;
            justify-content: space-between;
            border-bottom: 1px solid rgba(51, 51, 51, 0.2);
            @include breakpoint(md) {
                padding: 10px 0;
            }
            img{
                max-width: 40%
            }
            .info{
                .product-title{
                    display: block;
                    @include font-style('Roboto', 500, 20px, 26px);
                    @include breakpoint(md) {
                        font-size: 16px;
                        line-height: 19px;
                    }
                }
                .product-serie{
                    @include font-style('Roboto', 400, 16px, 19px);
                    margin: 20px 0;
                    color: rgba(51, 51, 51, 0.8);
                    @include breakpoint(md) {
                        margin: 10px 0;
                        font-size: 14px;
                        line-height: 16px;
                    }
                }
                .wrapper{
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    p{
                        &:first-child{
                            @include font-style('Rubik', 400, 16px, 19px);
                            width: fit-content;
                            @include breakpoint(md) {
                                font-size: 14px;
                                line-height: 16px;
                            }
                        }
                        &:last-child{
                            @include font-style('Rubik', 400, 26px, 19px);
                            @include breakpoint(md) {
                                font-size: 16px;
                                line-height: 21px;
                            }
                        }
                    }
                }
            }
        }
    }
}

.enter-coupon{
    padding: 20px;
    max-width: 580px;
    width: 100%;
    .coupon-heading{
        @include font-style('Poppins', 600, 24px, 36px);
        margin-bottom: 10px;
        @include breakpoint(md) {
            font-size: 16px;
            line-height: 21px;
        }
    }
    form{
        display: flex;
        align-items: center;
        justify-content: space-between;
        span{
            @include font-style('Rubik', 400, 26px, 21px);
            margin-left: 20px;
            @include breakpoint(md) {
                font-size: 20px;
                line-height: 21px;
            }
        }
    }
}